},
/*  , 1-2-*/
remind(id,type){
let self=this;
API.order.orderRemind({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
type:type,
}).then((res)=>{
if(res.body.code==200){
if(type==1){
self.confrim="";
}else{
self.confrim="";
}
self.currentPage=1;
self.toast=true;
localStorage.setItem("reload","1");
}
});
},
/* ,, */
download(){
this.$router.push({
path:'/download'
})
}
},mounted(){

},
activated(){
clearInterval(this.inter);
this.orderId=this.$route.params.id;
this.Initialization();
}
}



