
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import { API,getQuery } from '../../services';

export default {
components: {
Scroller,
Spinner,
XButton,
Group,
Cell,
LoadMore,
Toast
},
data () {
return {
/*  */
temporary:[],
paydata:{},
confrim:"",
toast:false,
showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20,
currentType:0,/* 0-66 */
/*  */
typeList:[{
name:"",
type:0,
},{
name:"",
type:1,
},{
name:"",
type:2,
},{
name:"",
type:3,
},{
name:"",
type:4,
},{
name:"",
type:5,
},{
name:"",
type:6,
},],
/*  */
timeMap:{1:"",2:"",3:"",4:"",5:""},
/*  */
orderList:[],
currentPage:1,
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),

},
mounted () {
localStorage.setItem("orderScroll",0);
this.currentPage=1;
this.getTypeData();
setInterval(()=>{
if(localStorage.getItem("reload")){
this.currentPage=1;
this.getTypeData();
localStorage.setItem("reload","");
}
},1000);
},
activated(){
setTimeout(()=>{
if(localStorage.getItem("orderScroll")){
document.querySelector(".order_list").scrollTop=localStorage.getItem("orderScroll");
}
},100);
},
methods: {
/* typelist */
typeselect(index){
this.currentType=index;
},
/*  */
getInfo(id){
window.location.href="/#/orderAction/"+id;
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
},
/*  */
confrimType(item){
if (item.order_is_delete == 0) {
if (item.order_refund_goods_status == 0) {
if (item.order_status == 1) {
if(item.order_shipping_status == 8){
item.orderType=8;//
}else{
item.orderType=1;//
}
} else if (item.order_status == 2) {
switch (item.order_shipping_status) {
case 1:
item.orderType=2;//
break;
case 2:
item.orderType=3;//
break;
case 3:
item.orderType=4;//
break;
case 4:
item.orderType=5;//
break;
case 5:
item.orderType=5;//
item.srcorderType=2;
break;
case 6:
item.orderType=6;//
break;
case 7:
item.orderType=7;//
break;
}
}
} else if (item.order_refund_goods_status == 1) {
item.orderType=9;//
} else if (item.order_refund_goods_status == 2) {
if (item.order_status == 2) {//
if (item.order_shipping_status == 2) {
item.srcorderType=0;//
} else if (item.order_shipping_status == 3) {
item.srcorderType=0;//
} else if (item.order_shipping_status == 5) {
item.srcorderType=1;//
} else if (item.order_shipping_status == 6) {
item.orderType=10;//
}
}
} else if (item.order_refund_goods_status == 4) {
if (item.order_shipping_status == 6) {
item.orderType=10;//
}
}else if(item.order_refund_goods_status == 3){
item.srcOrderType=3;//
}
}
},
/*  */
getTypeData(){
if(this.currentPage==1){
this.temporary=[];
}
API.order.orderlist({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderStatus:0,
page_number:10,
page:this.currentPage,
}).then((res)=>{
if(res.body.code==200){
let list= res.body.data.orderList.data
for(let item of list) {
this.confrimType(item);
this.temporary.push(item);
}
if(list.length==10){
this.currentPage++;
this.getTypeData();
}else{
this.orderList=this.temporary;
return false;
}
/* this.orderList=list; */
}
});
},
/*  */
deletOrder(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm () {
/*  */
API.order.orderDel({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="";
self.toast=true;
self.currentPage=1;
self.getTypeData();
}
});
}
});

},
/*  */
onBridgeReady(){
let self=this;
WeixinJSBridge.invoke(
'getBrandWCPayRequest',self.paydata,
function(res){
if(res.err_msg =="get_brand_wcpay_request:fail")  {
alert(JSON.stringify(self.paydata));
alert(JSON.stringify(res));
}
if(res.err_msg == "get_brand_wcpay_request:ok" ) {
self.confrim="";
self.toast=true;
self.currentPage=1;
self.getTypeData();
}     // ,res.err_msg    ok
}
);
},
/*  */
payOrder(id){
/*  */
let self=this;
API.order.orderShipPay({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderId:id,
}).then((res)=>{
let openId=localStorage.getItem("openId");
API.order.OrderWechat({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderSn:res.body.data.order_big_sn,
payMethod:3,
openId:openId,
}).then((resopndy)=>{
this.paydata=resopndy.body;
console.log(resopndy.body);
if (typeof WeixinJSBridge == "undefined"){
if( document.addEventListener ){
document.addEventListener('WeixinJSBridgeReady', self.onBridgeReady, false);
}else if (document.attachEvent){
document.attachEvent('WeixinJSBridgeReady', self.onBridgeReady);
document.attachEvent('onWeixinJSBridgeReady', self.onBridgeReady);
}
}else{
self.onBridgeReady();
}
},(err)=>{
alert(JSON.stringify(err));
});
})

},
/*  */
confrimOrder(id){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm () {
/*  */
API.order.orderReceipt({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
}).then((res)=>{
if(res.body.code==200){
self.confrim="";
self.currentPage=1;
self.getTypeData();
self.toast=true;
}
});
}
});
},
/*  */
confirmReturn(item){
/* * sinceId 
* expressId 
* logisticsName 
* revertId 
* company  */
let self=this;
if(item.order_express_type==2){
this.$vux.confirm.show({
/* title: 'Title', */
content: '',
onConfirm () {
/*  */
API.order.orderReturn({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:item.order_id,
sinceId:"",
expressId:"",
logisticsName:"",
revertId:"",
company:"",
}).then((res)=>{
if(res.body.code==200){
self.confrim="";
self.currentPage=1;
self.getTypeData();
self.toast=true;
}
});
}
});
}else{
console.log(item);
this.$router.push({ path: '/orderReturn?type='+item.order_express_type+"&orderId="+item.order_id});
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
}

},
/*  */
confrimSettlement(id){
this.$router.push({path:'/settlement',query:{
type:1,
id:id,
}});
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
},
/*  */
seeSettlement(id){
window.location.href="/#/settlement?id="+id;
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
},
/*  , 1-2-*/
remind(id,type){
let self=this;
API.order.orderRemind({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:id,
type:type,
}).then((res)=>{
if(res.body.code==200){
if(type==1){
self.confrim="";
}else{
self.confrim="";
}
self.currentPage=1;
self.toast=true;
}
});
},
/* ,, */
download(id){
window.location.href='/#/download';
localStorage.setItem("orderScroll",document.querySelector(".order_list").scrollTop);
}
}
}






